.team {
  display: flex;
  border: 1px solid black;
  padding: 15px;
}

.team .teamA,
.team .teamB {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;

  margin-left: 30px;
  width: 600px;
}

.summoner_data {
  display: flex;
}

.champion_image {
  width: 50px;
  height: 50px;
}

.item_container .item {
  width: 25px;
  height: 25px;
}
.summoner_data .summoner_name {
  width: 180px;
  margin-left: 10px;
}

.summoner_data .summoner_kda {
  width: 80px;
  text-align: center;
}

.summoner_data .spell_image_container {
  display: flex;
  flex-direction: column;
}

.spell_image_container .spell_image {
  width: 25px;
  height: 25px;
}
